<template>
  <v-container id="inventory" text-xs-center>
    <div>
      <p class="overline">100개 이상(<span class="green">녹색</span>) / 30개 이상 100개미만(<span class="yellow">노랑색</span>) / 2개 이상 30개 미만(<span class="red">빨강색</span>) / 1개 이하(<span class="grey">회색</span>) / 집계 안됨(<span>흰색</span>)</p>
      <v-select
        :items="addressSel"
        v-model="addressSelChoice"
        label="지역선택"
      ></v-select>
      <div v-if="addressSelChoice == '서울시'">
        <v-btn color="secondary" 
          dark 
          v-for="(adds, index) in address1"
          :key="index"
          @click="getStoreBy(adds)"
        >
          {{ adds.split(' ')[1] }}
        </v-btn>
      </div>
      <div v-if="addressSelChoice == '경기도'">
        <v-btn color="secondary" 
          dark 
          v-for="(adds, index) in address2"
          :key="index"
          @click="getStoreBy(adds)"
        >
          {{ adds.split(' ')[1] }}
        </v-btn>
      </div>
    </div>
    <v-list subheader>
        <v-list-item
          v-for="(store, index) in stores"
          :key="index"
        >
          <v-list-item-content>
            <v-list-item-title>{{ store.name }} <span class="check" :class="getColor(store.remain_stat)"></span></v-list-item-title>
            <v-list-item-subtitle v-text="store.addr"></v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-icon @click="getGoogleMap(store.lat, store.lng)">
            <i class="material-icons">
              add_location
            </i>
          </v-list-item-icon>
        </v-list-item>
    </v-list>
  </v-container>
</template>

<script>
import axios from 'axios';
import { API_URL } from '^@/API';
import { globalEvent } from '^@/event';
export default {
  name: 'Sales',
  data: () => ({
    paging: {
      page:1,
      length: 5,
      totalCount: null
    },
    addressSel: ['서울시', '경기도', '인천광역시', '강원도', '충청북도', '충청남도', '세종특별자치시', '대전광역시', '경상북도', '경상남도','대구광역시','울산광역시','전라북도','전라남도','광주광역시','제주특별자치도'],
    addressSelChoice:'',
    // 서울
    address1: ['서울특별시 강남구', '서울특별시 강동구', '서울특별시 강북구', '서울특별시 강서구', '서울특별시 관악구', '서울특별시 광진구', '서울특별시 구로구', '서울특별시 금천구', '서울특별시 노원구', '서울특별시 도봉구', '서울특별시 동대문구', '서울특별시 동작구', '서울특별시 마포구', '서울특별시 서대문구', '서울특별시 서초구', '서울특별시 성동구', '서울특별시 성북구', '서울특별시 송파구', '서울특별시 양천구', '서울특별시 영등포구', '서울특별시 용산구', '서울특별시 은평구', '서울특별시 종로구', '서울특별시 중구', '서울특별시 중랑구'],
    address2: ['경기도 수원시','경기도 고양시','경기도 성남시','경기도 부천시','경기도 안양시','경기도 광명시','경기도 평택시','경기도 안산시','경기도 과천시','경기도 오산시','경기도 시흥시','경기도 군포시','경기도 의왕시','경기도 하남시','경기도 용인시','경기도 이천시','경기도 안성시','경기도 김포시','경기도 화성시','경기도 광주시','경기도 의정부시','경기도 동두천시','경기도 구리시','경기도 남양주시','경기도 파주시','경기도 양주시','경기도 포천시','경기도 여주시','경기도 연천군','경기도 가평군','경기도 양평군'],
    stores: null
  }),
  methods: {
    getStoreBy(add){
      globalEvent.$emit('updateLoader', true);
      const params = {
        address: add,
      }
      axios.get(API_URL+'/storesByAddr/json', {params: params})
        .then(res => {
          console.log(res);
          this.stores = res.data.stores;
          this.paging.page = parseInt(res.data.page);
          this.paging.totalCount = res.data.totalCount;
          this.paging.length = res.data.totalPages;
          globalEvent.$emit('updateLoader', false);
        })
    },
    getColor(color){
      if(color == 'plenty'){
        return 'green'
      }else if(color == 'some'){
        return 'yellow'
      }else if(color == 'few'){
        return 'red'
      }else if(color == 'empty'){
        return 'grey'
      }
    },
    getGoogleMap(lat, lng){
      window.open(`https://www.google.com/maps?q=${lat},${lng}&ll=${lat},${lng}&z=15`)
    }
  }
}
</script>

<style lang="scss" scoped>
  button {
    margin:2px;
  }
  .check {
    display:inline-block;
    width:10px;
    height:10px;
    margin-top:-4px;
    border-radius:50%;
    vertical-align: middle;
  }
</style>
